import React from 'react';
import { CollectionItem, Row, Col } from 'react-materialize';

import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import 'react-awesome-slider/dist/styles.css';

import banner_1 from "./media/img/banner_1.png";
import banner_2 from "./media/img/banner_2.png";
import banner_3 from "./media/img/banner_3.png";
import tom from "./media/img/tom_ridge.png";

const banner_ids = [
  {
    'img': banner_1,
    'title': <Row>
      <Col s={5} >
        <img className="collection-item tom_ridge" src={tom} />
      </Col>
      <Col s={7}>
        <h4>Governor Tom Ridge</h4>
        <h6>First United States Secretary of Homeland Security </h6>
        <p className='italic orange-text'>"Our Goal is to Deter any potential attack with multiple levels of security"</p>
      </Col>
    </Row>,
    'desc': '',
    'btn': ''
  },
  {
    'img': banner_1,
    'title': 'Detection',
    'desc': 'Guardian One employs video and AI technology to provide immediate detection ',
    'btn': <button onClick={() => { window.location = '/about' }} className='btn btn-lrg white black-text'>Read More</button>,
  },
  {
    'img': banner_2,
    'title': 'Identify',
    'desc': 'Instant identification of potential threats',
    'btn': <button onClick={() => { window.location = '/shop' }} className='btn btn-lrg white black-text'>Read More</button>,
  },
  {
    'img': banner_3,
    'title': 'Communication',
    'desc': 'Upon detection of potential threats alerts are immediately sent to first responders',
    'btn': <button onClick={() => { window.location = '/about' }} className='btn btn-lrg white black-text'>Read More</button>,
  },
];

class Home extends React.Component {
  render() {
    return (
      <AutoplaySlider
        play={true}
        cancelOnInteraction={false} // should stop playing on user interaction
        interval={6000}
      >
        {
          banner_ids.map(
            id =>

              <div  data-src={id.img} >
                <div className='container-box'>
                <div className="collection" >
                  <div class="flow-text  overlay-box shadow white-text">
                    <h2 className='collection-item'>
                      {id.title}
                    </h2>
                    <div className='collection-item'>
                      {id.desc}
                    </div>
                    <div className='collection-item'>{id.btn}</div>
                  </div>
                </div>
              </div>
              </div>
          )
        }
      </AutoplaySlider>
    );
  }
}
const AutoplaySlider = withAutoplay(AwesomeSlider);

export default Home;





